
.name {
    position: absolute;
    top: 1.2rem;
    left: 2rem;
    position: absolute;
    top: 1.5rem;
    left: 4vw;
    font-weight: bold;
    text-transform: uppercase;
}
.progressbar {
    display: flex;
    width: 80vw;
    height: 4rem;
    border: 3px solid #f3f3f3;
    margin: 0px auto;
    position: relative;
    border-radius: 6px;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        
        font-weight: bold;

    }

    &__value {

    }

    & .plan-marker {
        
        display: block;
        position: absolute;
        width: 6px;
        height: 60px;
        top: 0;
        background-color: rgb(245, 69, 16);


        &::after {
            content: "Plan";
            position: absolute;
            top: -30px;
            left: -16px;
            color: red;
            text-transform: uppercase;
            font-weight: bold;
        }

    }
}

.x-axis {
    display: flex;
    margin: 0px auto;
    width: 80vw;
    position: relative;

}

.point {
    display: block;

    &__number {
        position: absolute;
        transform: translateX(-50%);
        //right: -24px;

        &::before {
            content: "";
            display: inline-block;
            position: absolute;
            width: 1px;
            height: 20px;
            right: 50%;
            background-color: black;
            top: -1.1rem;
        }
    }

}

.percentage {
    display: flex;
    justify-content: space-evenly;
    width: 50vw;
    position: absolute;
    top: -2.5rem;
    left: 32vw;
    font-size: 1.2rem;
}

